import React, { Component } from "react"
import { Link } from "gatsby"
import { FaCalendarAlt } from "react-icons/fa"
import DropdownMenu from "../components/dropdown"
import Img from "gatsby-image"
import Layout from "../components/layout"
import SplosnaKomunikacija from "./komunikacija-splosna"

class Blog extends Component {
  constructor(props) {
    super(props);

  }
  render() {
    return (
      <Layout>
        <div className="blog">
          <div className="blog-content">
            <div className="blog-item" /* style={{
              border: '40px solid transparent',
              borderImage: `url("https://mdn.mozillademos.org/files/6011/border-image-6.svg")`,
              padding: '15px',
            }} */ >
              <div className="blog-title">
                Bel list
              </div>
              <div className="blog-text pisava-17">Si kdaj pomislil, da ko se rodi, si kot bel, nepopisan list. Nato prve zapise, na ta tvoj lep beli list,
              naredijo tvoji stari, kasneje jim seveda pomagajo tudi stari stari (ker bog ne daj, brez njih res
              ne gre ;)). Nekaj asa so oni tvoji uitelji, vodii, vsevedi in mogoe e kakna stareja sestra ali
              brat, za povrh, da je mera polna <span className="emoji"></span>. Ja, prav vsi te uijo, usmerjajo in itak vedo kaj je najbolje zate.
              Kasneje se temu klanu pridrui vrtec z vsemi pripadajoimi akterji (vrstniki in vzgojitelji),
              nato ola isti koncept zasedbe, samo v irem krogu. To se nadaljuje skozi cel tvoj proces olanja.
              Zraven pa puajo svojo sled, na tem udovitem listu, seveda tudi prijateljitisti ta najbolji in
              tisti malo manj bolji.
              <br />
              Potem nastopi ta resen del.sluba. Okolica in sodelavci pridno igrajo svojo vlogo pri pisanju
              na tvoj list.
              <br />
              Nato spozna Ljubezen svojega ivljenja (e ne gre v prvo, pa mogoe v drugo, tretje.no,
              kakorkoli e <span className="emoji"></span>).
              <br />
                <br />
              Takrat nastopi v svoji najbolji verziji, ki jo potegne na plano, ez vest ta balast. Na zaetku
              se ti razprejo krila, zadiha s polnimi pljui, ima zalet. In ko ima obutek, da bo poletel v svoji
              najbolji verzijiBAMpristanek na zadnjico in dobrodoel v realnosti. Goriva je zmanjkalo ali
              smetana se je polizala.
              <br />
              Na plano pridejo vsi ti tvoji zapisi. In velikokrat niti ne porajta, da pravzaprav razmilja,
              obuti, razume kot si bil nauen. Nekje globoko v sebi pa ti tihi glasek govori, da mogoe pa le
              ni isto vse tako ali prav, kot si vedel in znal da sedaj. Da mogoe obstaja kakna drugana
              realnost, tvoja realnost. Mogoe pa ti to, karkoli e, lahko izpelje, se lahko naui, priui, si
              dovoli povedati na glas kaj si misli in kako razmilja, si dovoli drugae obutiti, si dovoli
              obutiti na sploh in si doloene stvari tudi priznati.
              <br />
                <br />
              Mogoe, pa ti stvari LAHKO pelje skozi ivljenje drugae kot tvoji stari, prijatelji sodelavci...
              <br />
                <br />
                <strong>Vse je mogoe! Kar je v nai domiljiji je mogoe tudi v nai realnosti. Ampak POZOR!</strong>
                <br />
                <br />
              Ni se ne spremeni ez no in ni se ne bo zgodilo samo od sebe. Vsaka sprememba zahteva svoj
              as. Se zaveda, koliko radirke potrebuje in asa, da zradira, kar ti ne slui?
              <br />
                <br />

              Potrebno si je RESNINO eleti bolji, bolj po tvojih eljah naravnan dan in ivljenje.
              <br />
                <br />
              Ker na tem tvoje udovitem listu je veliko DOBRONAMERNIH zapisov (preprianj in vzorcev).
              <br />
                <br />
              In da pride do tega, kdo pravzaprav si TI TI in ne kdo si, kot te je definirala okolica in vsi njeni
              akterji prisotni v tvojem ivljenju, zato je potrebno delo na sebi. In za presei vse to, kar te
              zadruje, je potrebno zopet delati na sebi.
              <br />
                <br />
              Ja, e se odloi za delo na sebi, ti garantiram, ne bo ti dolgas in ne bo ti zmanjkalo dela  <span className="emoji"></span> .
              <br />
                <br />
              Je pa noro dober OBUTEK, ko se osvobodi doloenih spon, preprian, vzorcev...POLETI <span className="emoji"></span>
              </div>
            </div>
            <div className="blog-item">
              <div className="blog-title">
                Preprianje, vzrok in posledica
              </div>
              <div className="blog-text pisava-13">
                Vsi ste e sliali za vzrok in posledico ampak ali ste se kdaj tudi dejansko poglobili v pomen tega?
              <br />
                <br />
                Jaz se vrsto let nisem, priznam. Itak mi je bilo kristalno jasno kaj to pomeni, e celo domiljala sem si, da zaznam to sosledje, ko se mi pripeti v ivljenju. Pa kako sem se motila. Sem morala parkrat pristati na riti, da sem se zaela poglabljati v svoje ivljenje in ga analizirati. Kmalu sem ugotovila, da ima kljuno vlogo nae preprianje. Zaradi njega, delujemo po doloenem konceptu, ki pripelje do vzroka in posledice. Potem pa imamo tukaj prisotno e popolnoma drugaen nain podajanja informacij pri mokih kot pri enskah. Mislim res dri, da znamo me eno dokaj preprosto zadevo povedati tako slikovito, malce bolj na dolgo in iroko (ker pa rade uporabljamo pridevnike, prislove ipd <span className="emoji" role="img"></span>), da nekdo ki ti dobro ne sledi, se lahko vmes izgubi. Doim`r moki, to je pa isto druga plat. Kratko in jedrnato, vasih celo pomanjkljivo <span className="emoji" role="img"></span>. No, potem pa se dajmo zastopit . Ni zastonj pregovor moki so iz Marsa, enske pa iz Venere.
                <br />
                <br />
                Pa da razloim malo bolj podrobno.
                <br />
                <br />
                Kot otroka so me vzgojili v duhu delavnosti. Od jutra do veera je "treba" skoz neki delat. V slubi, pa potem doma, pa e malo pri sosedih ( e sluajno zmanjka dela doma, kar se pa obiajno ne zgodi to vemo, a ne <span className="emoji" role="img"></span>) - <i><strong>preprianje</strong></i>. In posledino skozi to, sem potihoma to delavnost vpeljala v svoje ivljenje. Slubeno in privat. Moj takratni partner (mo in kasneje bivi mo, zato sem, napisala takratni <span className="emoji" role="img"></span>) pa je v najin zakon prinesel svoja preprianja, ki so seveda en malce odstopala od mojih. To je seveda popolnoma razumljivo, ker izhajava iz razlinih celic (primarne druine) in vsak od naju je s seboj prinesel svoja preprianja in vzorce.
                <br />
                <br />
                Vam je e jasno, kam pes taco moli?? <span className="emoji" role="img"></span><span className="emoji" role="img"></span>
                <br />
                <br />
                Ker sva imela razline poglede na doloene situacije in ker se nisva znala pogovarjat, izraziti, kaj ele posluati je velikokrat prilo, da je bila slika brez tona <span className="emoji" role="img"></span> (to je bil <strong><i>vzrok</i></strong> najinih preprianj). Zdaj ko gledam nazaj, je prav hecno, kako sva v marsikateri situaciji merila moi<span className="emoji" role="img"></span>. Jaz z besedam, ker e od nekdaj sem bila vea v komunikaciji (pa pustimo  malenkost , da imamo pravo in nepravo komunikacijo <span className="emoji" role="img"></span>  ), on z neodzivnostjo ali molkom. Vasih sva trmarila in vlekla vsak v svojo stran, kot da vleeva vrv ipd. Vmes pa sva e dovoljevala vmeavanje (direktno in indirektno) okolice. In ker se take situacije nikoli ne vleejo v nedogled, je sledila <i><strong>posledica</strong></i> - loitev. In vsak od naju je doprinesel 50:50 k situaciji.
                <br />
                <br />
                In ele sedaj, po parih letih dela na sebi, sem dojela koncept <strong>preprianje/vzrok/posledica</strong>.
                <br />
                <br />
                Zato dobro poglejte, dajte pod drobnogled, podroja svojega ivljenja, kjer se vam zatika. Preverite kakna so vaa preprianja. Prisluhnite razlagi vzroka, da prepreite posledico. Ali pa jo izpeljite (posledico namre) in dano situacijo izkoristite za osebno rast.
                <br />
                <br />

                Up to you <span className="emoji" role="img"></span>

              </div>
            </div>
            <div className="blog-item">
              <div className="blog-title">
                Preprianje, vzrok in posledica
              </div>
              <div className="blog-text pisava-19">
                Vsi ste e sliali za vzrok in posledico ampak ali ste se kdaj tudi dejansko poglobili v pomen tega?
              <br />
                <br />
                Jaz se vrsto let nisem, priznam. Itak mi je bilo kristalno jasno kaj to pomeni, e celo domiljala sem si, da zaznam to sosledje, ko se mi pripeti v ivljenju. Pa kako sem se motila. Sem morala parkrat pristati na riti, da sem se zaela poglabljati v svoje ivljenje in ga analizirati. Kmalu sem ugotovila, da ima kljuno vlogo nae preprianje. Zaradi njega, delujemo po doloenem konceptu, ki pripelje do vzroka in posledice. Potem pa imamo tukaj prisotno e popolnoma drugaen nain podajanja informacij pri mokih kot pri enskah. Mislim res dri, da znamo me eno dokaj preprosto zadevo povedati tako slikovito, malce bolj na dolgo in iroko (ker pa rade uporabljamo pridevnike, prislove ipd <span className="emoji" role="img"></span>), da nekdo ki ti dobro ne sledi, se lahko vmes izgubi. Doim`r moki, to je pa isto druga plat. Kratko in jedrnato, vasih celo pomanjkljivo <span className="emoji" role="img"></span>. No, potem pa se dajmo zastopit . Ni zastonj pregovor moki so iz Marsa, enske pa iz Venere.
                <br />
                <br />
                Pa da razloim malo bolj podrobno.
                <br />
                <br />
                Kot otroka so me vzgojili v duhu delavnosti. Od jutra do veera je "treba" skoz neki delat. V slubi, pa potem doma, pa e malo pri sosedih ( e sluajno zmanjka dela doma, kar se pa obiajno ne zgodi to vemo, a ne <span className="emoji" role="img"></span>) - <i><strong>preprianje</strong></i>. In posledino skozi to, sem potihoma to delavnost vpeljala v svoje ivljenje. Slubeno in privat. Moj takratni partner (mo in kasneje bivi mo, zato sem, napisala takratni <span className="emoji" role="img"></span>) pa je v najin zakon prinesel svoja preprianja, ki so seveda en malce odstopala od mojih. To je seveda popolnoma razumljivo, ker izhajava iz razlinih celic (primarne druine) in vsak od naju je s seboj prinesel svoja preprianja in vzorce.
                <br />
                <br />
                Vam je e jasno, kam pes taco moli?? <span className="emoji" role="img"></span><span className="emoji" role="img"></span>
                <br />
                <br />
                Ker sva imela razline poglede na doloene situacije in ker se nisva znala pogovarjat, izraziti, kaj ele posluati je velikokrat prilo, da je bila slika brez tona <span className="emoji" role="img"></span> (to je bil <strong><i>vzrok</i></strong> najinih preprianj). Zdaj ko gledam nazaj, je prav hecno, kako sva v marsikateri situaciji merila moi<span className="emoji" role="img"></span>. Jaz z besedam, ker e od nekdaj sem bila vea v komunikaciji (pa pustimo  malenkost , da imamo pravo in nepravo komunikacijo <span className="emoji" role="img"></span>  ), on z neodzivnostjo ali molkom. Vasih sva trmarila in vlekla vsak v svojo stran, kot da vleeva vrv ipd. Vmes pa sva e dovoljevala vmeavanje (direktno in indirektno) okolice. In ker se take situacije nikoli ne vleejo v nedogled, je sledila <i><strong>posledica</strong></i> - loitev. In vsak od naju je doprinesel 50:50 k situaciji.
                <br />
                <br />
                In ele sedaj, po parih letih dela na sebi, sem dojela koncept <strong>preprianje/vzrok/posledica</strong>.
                <br />
                <br />
                Zato dobro poglejte, dajte pod drobnogled, podroja svojega ivljenja, kjer se vam zatika. Preverite kakna so vaa preprianja. Prisluhnite razlagi vzroka, da prepreite posledico. Ali pa jo izpeljite (posledico namre) in dano situacijo izkoristite za osebno rast.
                <br />
                <br />

                Up to you <span className="emoji" role="img"></span>

              </div>
            </div>
          </div>
        </div>
      </Layout>
    )
  }

}

export default Blog
